/* Tocify */
.tocify {
  width: 20%;
  max-height: 90%;
  overflow: auto;
  position: fixed;
  border: 1px solid #E5E5E5;
  border-radius: 0px;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.2);
}
.tocify ul,
.tocify li {
  list-style: none;
  margin: 0;
  padding: 0;
  border: none;
  line-height: 30px;
}
.tocify ul.tocify-header > li > a {
  border-bottom: 1px solid #E5E5E5;
}
.tocify ul.tocify-subheader {
  display: none;
}
.tocify ul.tocify-subheader li.tocify-item a {
  background: #F5F5F5;
}
.tocify ul.tocify-subheader li.tocify-item a:hover {
  background: #F0F0F0;
}
.tocify ul.tocify-subheader li.tocify-item.active a {
  background: #1b1e24;
}
.tocify li.tocify-item a {
  background: #FFF;
  padding: 10px;
  font-size: 12px;
  color: #333;
  line-height: 20px;
  border-bottom: 1px solid #E5E5E5;
}
.tocify li.tocify-item a:hover {
  background: #F5F5F5;
}
.tocify li.tocify-item.active a {
  background: #1b1e24;
  color: #FFF;
}
.tocify-header {
  text-indent: 10px;
}
.tocify-subheader {
  text-indent: 20px;
}
.tocify-subheader li {
  font-size: 12px;
}
.tocify-subheader .tocify-subheader {
  text-indent: 30px;
}
.tocify-subheader .tocify-subheader .tocify-subheader {
  text-indent: 40px;
}
/* END Tocify */
